<template>
    <ul class="list" :class="{scroll:isScroll==='Yes'}">
      <li class="activity-item" v-for="(support, index) in info.supports" :key="index" :class="activityClasses[support.type]">
        <span class="content-tag">
          <span class="mini-tag">{{support.name}}</span>
        </span>
        <span class="activity-content">{{support.content}}</span>
      </li>
    </ul> 
</template>

<script>
  import {mapState} from 'vuex'
  export default {
    props:{
      isScroll:String
    },
    // 当前组件决定元素类名是否显示的状态
    data() {
      return {
        activityClasses: ['activity-green', 'activity-red', 'activity-orange']
      }
    },
    computed:{
      ...mapState(['info'])
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import '../../assets/stylus/minxins'
    .list
      font-size 16px
      &.scroll
        height 160px
        overflow-y auto
      .activity-item
        margin-bottom 12px
        display flex
        font-size 13px
        align-items center
        &.activity-green
          .content-tag
            background-color rgb(112, 188, 70)
        &.activity-red
          .content-tag
            background-color rgb(240, 115, 115)
        &.activity-orange
          .content-tag
            background-color: rgb(241, 136, 79)
        .content-tag
          display inline-block
          border-radius 2px
          width 36px
          height: 18px
          margin-right 10px
          color #fff
          font-style normal
          position relative
          .mini-tag
            position absolute
            left 0
            top 0
            right -100%
            bottom -100%
            font-size 24px
            transform scale(.5)
            transform-origin 0 0
            display flex
            align-items center
            justify-content center
</style>





